@import url(https://use.typekit.net/drz2vvu.css);
@import url(https://fonts.googleapis.com/css2?family=Tenali+Ramakrishna&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{--base--white: #ffffff;--base--black: #050505;--base--grey: #242424;--base--green: #1db954;--base--grey--dark: #131313;--border-radius--sm: 8px;--border-radius--lg: 50px;--spacer--sm: 20px;--spacer--lg: 60px}html{scroll-behavior:smooth;box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}body{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-family:"proxima-nova", sans-serif;background:#131313;background:var(--base--grey--dark);color:#ffffff;color:var(--base--white);text-align:center}a{color:#ffffff;color:var(--base--white);text-decoration:none}.app--header{font:normal 32px/35px "proxima-nova", sans-serif;padding-top:60px;padding-top:var(--spacer--lg)}

.command{align-items:center;display:flex;justify-content:center;text-align:center;padding-top:var(--spacer--lg)}.command .command--input{border-radius:var(--border-radius--lg);font-size:17px;max-width:100%;padding:8px;width:546px}.command .command--note{padding:var(--spacer--sm) 0}.command .command--actions{padding:var(--spacer--lg);display:flex;flex-direction:column;margin:0 auto}.command .command--actions .command--action{background:var(--base--green);border:none;margin:var(--spacer--sm)}.command .command--action,.command .commmand-query{border-radius:var(--border-radius--lg);cursor:pointer;font:12px/15px "Proxima Nova", sans-serif;font-weight:600;width:102px;padding:3px 10px;text-transform:uppercase}.command .commmand-query{background:none;border:1px solid var(--base--white);color:var(--base--white);height:32px;margin-left:var(--spacer--sm)}.command .command--response{align-items:center;background:var(--base--grey);display:flex;font:30px/35px "Tenali Ramakrishna", sans-serif;margin-bottom:var(--spacer--sm);min-height:200px;justify-content:center;width:100%;text-transform:lowercase}@media only screen and (min-width: 1024px){.command .command--actions{flex-direction:row}}

